@import '../../theme.scss';
.console {
    border-radius: 50%;
    display: flex;
    background: $primaryColorLight;
    padding: 3em;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    align-items: center;
    justify-content: center;
    .icon {
        max-width: 100px;
        max-height: 100px;
        width: 100%;
        height: 100%;
    }
}
